<template>
  <div class="role-list">
    <!--    <div class="header">-->
    <!--      <div class="filter">-->
    <!--        <div class="left">-->

    <!--        </div>-->

    <!--        <Form ref="formFilter" :model="formFilter" inline>-->
    <!--          <FormItem :label="$t('common.text.search')">-->
    <!--            <Input-->
    <!--              :placeholder="$t_p('role_name_placeholder')"-->
    <!--              class="filter-input"-->
    <!--              v-model="formFilter.name"-->
    <!--              @keyup.enter.native="loadTableData"-->
    <!--            />-->
    <!--          </FormItem>-->
    <!--          <FormItem prop="roleType" class="form-item" :label="$t_p('role_type')">-->
    <!--            <RoleTypeSelect-->
    <!--              class="filter-input"-->
    <!--              :show="true"-->
    <!--              v-model="formFilter.roleType"-->
    <!--              :placeholder="$t_p('role_type_placeholder')"-->
    <!--            ></RoleTypeSelect>-->
    <!--          </FormItem>-->
    <!--          <FormItem :label-width="10">-->
    <!--            <Button @click="loadTableData" :loading="serLoading" class="search-btn" type="primary">-->
    <!--              <Icon type="search"/>{{$t('common.message.query')}}-->
    <!--            </Button>-->
    <!--          </FormItem>-->
    <!--        </Form>-->
    <!--        <Button class="btn-create" type="primary" v-if="$hasAccess('role.add')"-->
    <!--                @click="$router.push({name:'role-new'})">{{$t('common.message.add_new')}}-->
    <!--        </Button>-->
    <!--      </div>-->
    <!--    </div>-->

    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">角色列表</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Input placeholder="请输入角色名称"
                   v-model="formFilter.name"
                   clearable
                   @keyup.enter.native="loadTableData" />
          </div>
          <div class="filter-select mr-12">
            <RoleTypeSelect :show="true"
                            v-model="formFilter.roleType"
                            placeholder="请选择角色类型"></RoleTypeSelect>
          </div>
          <div class="filter-btn">
            <Button type="default"
                    @click="loadTableData"
                    :loading="pageInfo.loading">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="t-btn">
            <Button @click="$router.push({name:'role-new'})">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               :row-class-name="(row,index)=>index%2===1?'even':''"
               :loading="serLoading"
               stripe
               :columns="tableColumns"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
        <!--<div class="page-total">共{{pageInfo.totalElements}}条</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import { deleteRole, getRoleList } from '@/api/role'
import RoleTypeSelect from '@c/role-type-select'

export default {
  name: 'role-list',
  data () {
    return {
      formFilter: {},
      serLoading: false,
      tableColumns: [
        {
          title: '角色名称',
          key: 'roleName',
          align: 'left'
        },
        {
          title: '角色类型',
          key: 'name',
          align: 'left'
        },
        {
          title: '角色描述',
          key: 'remark',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  style: {
                    color: 'rgba(0, 132, 255, 1)',
                    cursor: 'pointer'
                  },
                  on: {
                    click: () => {
                      this.editRole(params)
                    }
                  }
                },
                '编辑'
              ),
              h(
                'span',
                {
                  style: {
                    color: 'rgba(241, 33, 33, 1)',
                    cursor: 'pointer'
                  },
                  class: 'secondary',
                  on: {
                    click: () => {
                      // console.log(params)
                      this.deleteRole(params.row)
                    }
                  }
                },
                '删除'
              )
            ])
          }
        }
      ],
      tableData: [],
      pageInfo: {
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      }
    }
  },
  methods: {
    showColumn () {
      if (!this.$hasAccess('role.edit') && !this.$hasAccess('role.del')) {
        this.tableColumns.splice(this.tableColumns.length - 1, 1)
      }
    },
    // 初次加载表格
    loadTableData (clear) {
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      this.serLoading = true
      getRoleList({
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize,
        name: this.formFilter.name,
        roleType: this.formFilter.roleType
      }).then((res) => {
        this.serLoading = false
        this.tableData = (res.data.result || []).map((d) => ({
          ...d
        }))

        this.tableData.map((item) => {
          let user = item
          if (item.roleType === 1) {
            user.name = '管理者'
          } else if (item.roleType === 2) {
            user.name = '运营人员'
          } else if (item.roleType === 3) {
            user.name = '服务人员'
          } else if (item.roleType === 4) {
            user.name = '生产人员'
          }
          return user
        })
        this.pageInfo.totalElements = res.data.totalElements || 0
      })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },

    // 编辑表格
    editRole (params) {
      let uInfo = this.tableData[params.index]
      this.$router.push({
        name: 'role-edit',
        params: { roleId: uInfo.roleId }
      })
    },

    // 删除信息
    deleteRole (uInfo) {
      // console.log(uInfo);
      this.$Modal.confirm({
        title: this.$t('common.text.prompt'),
        width: this.$flexible(450),
        content: '确认删除角色名为' + `[${uInfo.roleName}]` + '的角色名吗?',
        onOk: () => {
          deleteRole({
            roleId: uInfo.roleId
          }).then(
            (res) => {
              if (res.code === 200) {
                this.$Message.success(this.$t('common.message.delete_success'))
                this.loadTableData()
              } else {
                this.$Message.warning(this.$t('common.message.delete_fail'))
              }
            }
          )
        }
      })
    }
  },
  mounted () {
    this.loadTableData(true)
    this.showColumn()
  },
  components: {
    RoleTypeSelect
  }
}
</script>

<style lang="less">
@import '../../../../style/varient';

.user-table {
  /*border:1px solid #d2d2d2;*/
}
</style>

<style lang="less" scoped>
@import '../../../../style/varient';

@size-padding: 10px;
.role-list {
  width: 100%;
  height: 100%;

  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 15px;
  }
}
</style>



